import "./styles.css";

const searchBtn = document.querySelector("#search-icon");
const searchInput = document.querySelector("#search-query");
const earseSearchInput = document.querySelector("#erase-search-bar");
const temprature = document.querySelector(".temprature");
const cityInWeather = document.querySelector(".city");
const countryInWeather = document.querySelector(".country");
const regionInWeather = document.querySelector(".region");
const errorMsg = document.querySelector(".error-msg");
const humidityPercentage = document.querySelector(".h-perc");
const cloudPercentage = document.querySelector(".cloud-perc");
const uvIndex = document.querySelector(".uv-perc");
const rainAmount = document.querySelector(".rain-perc");
const windTemprature = document.querySelector(".wind-temp-perc");
const feelsLikeTemp = document.querySelector(".feels-like-perc");
const overlay = document.querySelector(".overlay")

const defaultCity = "London";
let w = false;

searchInput.value = 'London';

searchBtn.addEventListener("click", () => {

  fetchWeatherData(searchInput.value);
});

earseSearchInput.addEventListener("click", () => {
  searchInput.value = "";
});

window.addEventListener("load", () => {
  w = true;
  overlay.classList.add("show");
  fetchWeatherData(defaultCity);
  
});

async function fetchWeatherData(city) {
  if (searchInput.value.length < 3 && w == false) {
    return;
  }

  const response = await fetch(
    `https://api.weatherapi.com/v1/	/forecast.json?key=beb038921e7242958e000018243006&q=${city}&days=14`,
    { mode: "cors" }
  );

  if (!response.ok) {
    errorMsg.classList.add("show");

    setTimeout(function () {
      errorMsg.classList.remove("show");
    }, 3000);
    return;
  }
  overlay.classList.add("show")
  const data = await response.json();
  overlay.classList.remove("show")
  const newWeatherObj = createCurrentSearchObject(data);
  updateLocationInfoDisplay(newWeatherObj);
 
}

function createCurrentSearchObject(weatherObj) {
  let city = weatherObj.location.name;
  let country = weatherObj.location.country;
  let region = weatherObj.location.region;
  let temp_c = weatherObj.current.temp_c;
  let humidity = weatherObj.current.humidity;
  let cloudCoverage = weatherObj.current.cloud;
  let uvI = weatherObj.current.uv;
  let chanceOfRain =
    weatherObj.forecast.forecastday[0].day.daily_chance_of_rain;

  let windTemprature = weatherObj.current.windchill_c;
  let tempFeelsLike = weatherObj.current.feelslike_c;

  return {
    city,
    country,
    region,
    temp_c,
    humidity,
    cloudCoverage,
    uvI,
    chanceOfRain,
    windTemprature,
    tempFeelsLike,
  };
}

function updateLocationInfoDisplay(currentWeatherObj) {
  cityInWeather.textContent = `${currentWeatherObj.city}`;
  countryInWeather.textContent = `- ${currentWeatherObj.country},`;
  regionInWeather.textContent = currentWeatherObj.region;
  temprature.innerHTML = `${currentWeatherObj.temp_c}&deg`;
  humidityPercentage.textContent = currentWeatherObj.humidity;
  cloudPercentage.textContent = currentWeatherObj.cloudCoverage;
  uvIndex.textContent = currentWeatherObj.uvI;
  rainAmount.textContent = currentWeatherObj.chanceOfRain;
  windTemprature.innerHTML = `${currentWeatherObj.windTemprature}&deg`;
  feelsLikeTemp.innerHTML = `${currentWeatherObj.tempFeelsLike}&deg`;
}
