___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    font-family: sans-serif;
    /* border: 1px solid red; */
}

body, html {
    margin: 0;
    
}

.main-page {
    width: 100vw;
    position: relative;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.836);
    
}

.search-bar-c {
    width: 100vw;
    min-height: 90px;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar {
    border-radius: 17px;
    width: 80%;
    padding: 9px 15px;
    display: flex;
    gap: 40px;
    justify-content: space-between;
    align-items: center;
    height: 60%;
    border: 1px solid #778da9;
}

#search-icon {
    font-size: 30px;
    cursor: pointer;
    color: white;
    transition: color 0.3s ease;
    
    
}

#search-icon:hover {
    color: #d0f4de;
}

#erase-search-bar {
    width: 24px;
    color: rgb(175, 6, 6);
    cursor: pointer;
    transition: color 0.3s ease;
   
}

#erase-search-bar:hover {
    color: red;
}

#search-query {
    flex-grow: 1;
    height: 100%;
    background-color: transparent;
    border: 0;
    outline: none;
    font-size: 17.5px;
    letter-spacing: 1.5px;
    color: white;
}

.todays-weather-c {
    width: 98vw;
    height: 80vh;
   
    padding-top: 20px;
    padding-inline: 15px;
}

.todays-weather {
    display: flex;
    width: 100%;
    height: 100%;
    position: relative;
    align-items: end;
    
    border-radius: 7px;
    
    background-color: rgba(255, 255, 255, 0.144);
   
}






.up-to-15days-forecast-c {

    width: 98vw;
    height: 620px;
    /* border: 1px solid red; */
    padding-top: 120px;
    padding-inline: 15px;
    padding-bottom: 10px;
}

.up-to-15days-forecast {
    min-width: 100%;
    height: 100%;
    
    border-radius: 7px;
    background-color: rgba(255, 255, 255, 0.144);
}

.up-to-15days-forecast {
    display: flex;
    overflow-y: auto;
    gap: 15px;
    padding: 5px 10px;
}

.overlay {
    width: 100vw;
    height: 100vh;
    background: #0000006b;
    backdrop-filter: blur(8px);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    visibility: hidden;
    display: none;
    opacity: 0;
    transition: all 0.3s ease;
}


.overlay.show {
    visibility: visible;
    opacity: 1;
    display: block;
}

.up-to-15days-forecast > * {
    flex-basis: 320px;
   
    flex-shrink: 0;
    border: 1px solid red;
}

.location-info {
    width: 350px;
    height: 150px;
    /* border: 1px solid red; */
    position: absolute;
    top: 7%;
    left: 3%;
}

.region-country {
    display: flex;
    gap: 8px;
    margin-top: -5px;
    letter-spacing: 1px;
    align-items: baseline;
    /* border: 1px solid red; */
   
}

.city {
    color: white;
    letter-spacing: -0.3px;
    font-size: 35px;
}

.country {
    color: #daf0ee;
    /* border: 1px solid red; */
    white-space: nowrap;
}

.region {
    color: #ccc5b9;
    /* border: 1px solid red; */
    white-space: nowrap;
    
}

.temprature-c {
    position: absolute;
    top: 7%;
    right: 0;
    width: 350px;
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    /* border: 1px solid green; */

}

.temprature {
    margin-right: -50px;
    font-size: 70px;
    margin-top: -10px;
    color: white;

}

.error-msg {

    width: 430px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    border-radius: 7px;
    white-space: nowrap;
    /* border: 1px solid red; */
    position: absolute;
    background-color: #001427;
    letter-spacing: 1px;
    font-size: 15px;
    transform: translate(-50%, -45px);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    left: 50%;
    color: red;
    top: 0;
}

.error-msg.show {
    opacity: 1;
    transform: translate(-50%, 15px);
    visibility: visible;
}


.weather-status-c {
    width: 100%;
    height: 480px;
    display: flex;
    padding: 0 10px;
    padding-bottom: 10px;
    flex-wrap: wrap;
    
    gap: 10px;
    
}

.weather-status-c > * {
    flex-basis: 240px;
    height: 230px;
    flex-grow: 1;
    border-radius: 7px;
   
    background-color: #ffffff75;
}

.humidiy-status, .rain-status, .cloud-status, .uv-status, .wind-temp-status, .feels-like-status {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 38px;
}

.humidiy-h-icon, .rain-h-icon, .cloud-h-icon, .uv-h-icon, .wind-temp-h-icon, .feels-like-h-icon {
    display: flex;
    flex-direction: column;
    
    justify-content: center;
    color: #cbdcf3;
    align-items: center;
    gap: 15px;
}

.icon-humidity, .icon-rain, .icon-cloud, .icon-uv, .icon-wind-temp, .icon-feels-like {
    font-size: 32px;
    font-weight: bold;
}

.h3-h, .h3-rain, .h3-cloud, .h3-uv, .h3-wind-temp, .h3-feels-like {
    font-size: 19px;
    letter-spacing: 0.8px;
    font-weight: bold;
}

.humidity-percentage, .rain-percentage, .cloud-percentage, .uv-percentage, .wind-temp-percentage, .feels-like-percentage {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
}

.h-perc, .rain-perc, .cloud-perc, .uv-perc, .wind-temp-perc, .feels-like-perc {
    font-size: 28px;
    /* transform: rotate(-1deg); */
    font-weight: bold;
    color: #cbdcf3;
}

.humidity-percentage > span, .rain-percentage > span, .cloud-percentage > span, .uv-percentage > span, .wind-temp-percentage > span, .feels-like-percentage > span {
    color: rgb(54, 54, 54);
    font-size: 19px;
    
}

.rain-perc {
    font-size: 28px;
}
